import React, { useState } from 'react';
import { LoginRequest } from '../../../types/auth';
import env from '../../common/env';
import { Button, Password, TextInput } from '../../components';
import { useAuthStore } from '../../services/auth';
import LoginMarketingBanner from './LoginMarketingBanner';
import PhoneVerificationModal from './PhoneVerificationModal';

const Login = () => {
    const login = useAuthStore((s) => s.login);
    const [formData, setFormData] = useState<LoginRequest>({
        loginId: '',
        password: '',
    });
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState('');
    const [showPhoneVerification, setShowPhoneVerification] = useState(false);
    const [userForVerification, setUserForVerification] = useState<any>(null);

    const handleInputChange = ({ value, name }: { value?: string; name?: string }) => {
        setFormData((prev) => ({ ...prev, [name!]: value }));
        if (error) setError('');
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!formData.loginId || !formData.password) {
            setError('Please fill in all fields');
            return;
        }

        setLoading(true);
        setError('');

        try {
            const response = await login(formData);

            if (response.user && !response.user.isActive) {
                if (env.PHONE_VERIFICATION_REQUIRED && !response.user.isPhoneVerified) {
                    setUserForVerification(response.user);
                    setShowPhoneVerification(true);
                } else {
                    setError('Account verification pending. Please check your email.');
                }
            } else {
                window.location.href = '/dashboard';
            }
        } catch (error: any) {
            setError(error?.message || 'Login failed. Please try again.');
        } finally {
            setLoading(false);
        }
    };

    const handleVerificationComplete = () => {
        setShowPhoneVerification(false);
        setUserForVerification(null);
        window.location.href = '/dashboard';
    };

    return (
        <div className="min-h-screen flex">
            <LoginMarketingBanner />

            {/* Login Form Section */}
            <div className="w-full lg:w-1/2 flex items-center justify-center p-8 bg-gray-50">
                <div className="w-full max-w-md">
                    <div className="bg-white rounded-2xl shadow-xl p-8">
                        <div className="text-center mb-8">
                            <div className="w-16 h-16 bg-linear-to-r from-blue-600 to-purple-600 rounded-xl mx-auto mb-4 flex items-center justify-center">
                                <span className="text-white text-2xl font-bold">OB</span>
                            </div>
                            <h2 className="text-3xl font-bold text-gray-900 mb-2">Welcome Back</h2>
                            <p className="text-gray-600">Sign in to your OmniBlast account</p>
                        </div>

                        <form onSubmit={handleSubmit} className="space-y-6">
                            <div>
                                <TextInput
                                    label="Login ID"
                                    required
                                    name="loginId"
                                    placeholder="Enter your login ID"
                                    value={formData.loginId}
                                    onChange={handleInputChange}
                                    disabled={loading}
                                    className="mt-1"
                                />
                            </div>

                            <div>
                                <Password
                                    label="Password"
                                    required
                                    name="password"
                                    placeholder="Enter your password"
                                    value={formData.password}
                                    onChange={handleInputChange}
                                    disabled={loading}
                                    className="mt-1"
                                />
                            </div>

                            {error && (
                                <div className="bg-red-50 border border-red-200 rounded-lg p-3">
                                    <p className="text-red-600 text-sm">{error}</p>
                                </div>
                            )}

                            <Button
                                type="submit"
                                className="w-full bg-linear-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 text-white py-3 rounded-lg font-semibold transition-all duration-200 transform hover:scale-105"
                                disabled={loading}
                            >
                                {loading ? 'Signing In...' : 'Sign In'}
                            </Button>
                        </form>

                        <div className="mt-8 text-center">
                            <p className="text-gray-600">
                                Don't have an account?{' '}
                                <a
                                    href="/register"
                                    className="text-blue-600 hover:text-blue-700 font-semibold hover:underline transition-colors"
                                >
                                    Sign up now
                                </a>
                            </p>
                        </div>
                    </div>

                    <div className="mt-8 text-center text-sm text-gray-500">
                        <p>Â© {new Date().getFullYear()} OmniBlast. All rights reserved.</p>
                    </div>
                </div>
            </div>

            {showPhoneVerification && userForVerification && (
                <PhoneVerificationModal
                    user={userForVerification}
                    onComplete={handleVerificationComplete}
                    onClose={() => setShowPhoneVerification(false)}
                />
            )}
        </div>
    );
};

export default Login;
