import React, { useEffect, useState } from 'react';
import { AuthResponse, ResendVerificationRequest, VerifyPhoneRequest } from '../../../types/auth';
import { Button, TextInput } from '../../components';
import ajax from '../../services/ajax';

interface PhoneVerificationModalProps {
    user: {
        id: string;
        name: string;
        email: string;
        phone?: string;
        isPhoneVerified: boolean;
    };
    onComplete: () => void;
    onClose: () => void;
}

const PhoneVerificationModal: React.FC<PhoneVerificationModalProps> = ({ user, onComplete, onClose }) => {
    const [code, setCode] = useState('');
    const [loading, setLoading] = useState(false);
    const [resending, setResending] = useState(false);
    const [error, setError] = useState('');
    const [timeLeft, setTimeLeft] = useState(0);

    useEffect(() => {
        let timer: NodeJS.Timeout;
        if (timeLeft > 0) {
            timer = setTimeout(() => setTimeLeft(timeLeft - 1), 1000);
        }
        return () => clearTimeout(timer);
    }, [timeLeft]);

    const handleVerify = async () => {
        if (!code || code.length !== 6) {
            setError('Please enter a valid 6-digit code');
            return;
        }

        setLoading(true);
        setError('');

        try {
            await ajax.post<AuthResponse>('/auth/verify-phone', {
                userId: user.id,
                code,
            } as VerifyPhoneRequest);

            onComplete();
        } catch (error: any) {
            setError(error?.message || 'Verification failed. Please try again.');
        } finally {
            setLoading(false);
        }
    };

    const handleResend = async () => {
        if (timeLeft > 0) return;

        setResending(true);
        setError('');

        try {
            await ajax.post<AuthResponse>('/auth/resend-verification', {
                type: 'phone',
                userId: user.id,
            } as ResendVerificationRequest);

            setTimeLeft(60);
            setCode('');
        } catch (error: any) {
            setError(error?.message || 'Failed to resend code. Please try again.');
        } finally {
            setResending(false);
        }
    };

    const formatTime = (seconds: number) => {
        const mins = Math.floor(seconds / 60);
        const secs = seconds % 60;
        return `${mins}:${secs.toString().padStart(2, '0')}`;
    };

    return (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
            <div className="bg-white rounded-2xl shadow-2xl max-w-md w-full p-8">
                <div className="text-center mb-6">
                    <div className="w-16 h-16 bg-green-100 rounded-full mx-auto mb-4 flex items-center justify-center">
                        <span className="text-green-600 text-2xl">ðŸ“±</span>
                    </div>
                    <h2 className="text-2xl font-bold text-gray-900 mb-2">Verify Your Phone</h2>
                    <p className="text-gray-600">
                        We've sent a 6-digit verification code to your phone number. Please enter it below to complete your registration.
                    </p>
                </div>

                <div className="space-y-4">
                    <div>
                        <TextInput
                            label="Verification Code"
                            name="code"
                            placeholder="Enter 6-digit code"
                            value={code}
                            onChange={({ value }) => {
                                setCode(value.replace(/\D/g, '').slice(0, 6));
                                if (error) setError('');
                            }}
                            disabled={loading}
                            maxLength={6}
                            className="mt-1 text-center text-2xl font-mono tracking-widest"
                        />
                    </div>

                    {error && (
                        <div className="bg-red-50 border border-red-200 rounded-lg p-3">
                            <p className="text-red-600 text-sm">{error}</p>
                        </div>
                    )}

                    <Button
                        onClick={handleVerify}
                        className="w-full bg-linear-to-r from-green-600 to-blue-600 hover:from-green-700 hover:to-blue-700 text-white py-3 rounded-lg font-semibold transition-all duration-200"
                        disabled={loading || code.length !== 6}
                    >
                        {loading ? 'Verifying...' : 'Verify Phone Number'}
                    </Button>

                    <div className="text-center">
                        <p className="text-gray-600 text-sm mb-2">Didn't receive the code?</p>
                        <Button
                            onClick={handleResend}
                            disabled={timeLeft > 0 || resending}
                            className="text-blue-600 hover:text-blue-700 font-semibold text-sm bg-transparent border-none p-0 h-auto"
                        >
                            {resending ? 'Sending...' : timeLeft > 0 ? `Resend in ${formatTime(timeLeft)}` : 'Resend Code'}
                        </Button>
                    </div>
                </div>

                <div className="mt-6 pt-4 border-t border-gray-200">
                    <Button
                        onClick={onClose}
                        className="w-full text-gray-600 hover:text-gray-700 bg-gray-100 hover:bg-gray-200 py-2 rounded-lg font-medium transition-colors"
                    >
                        Skip for Now
                    </Button>
                </div>
            </div>
        </div>
    );
};

export default PhoneVerificationModal;
